export const userLogin = (params) => {
	return request({
		url: '/user/user/login',
		method: 'POST',
		params
	}).then(res => {
		// 
		if (res.code === 1 || res.code === 200) {
			return {
				...res,
				data: {
					...res.data,
					deliveryFee: 6, // 
					shopName: "Sky",
					shopAddress: "10",
					shopId: 1
				}
			}
		}
		return res;
	})
}

