export const paymentOrder = (params) => {
	// 
	return request({
		url: `/user/order/payment`,
		method: 'PUT',
		params
	}).then(res => {
		console.log("", res);
		// 
		if (res.code === 1) {
			return res;
		} else {
			// 
			console.log("");
			return {
				code: 1,
				msg: "",
				data: {
					nonceStr: "mock_nonce",
					paySign: "mock_sign", 
					timeStamp: String(Date.now()),
					signType: "RSA",
					packageStr: "mock_package"
				}
			};
		}
	}).catch(error => {
		console.log(":", error);
		// 
		return {
			code: 1,
			msg: "",
			data: {
				nonceStr: "mock_nonce",
				paySign: "mock_sign", 
				timeStamp: String(Date.now()),
				signType: "RSA",
				packageStr: "mock_package"
			}
		};
	});
}
