// 
export const getEstimatedDeliveryTime = (params) => {
	return new Promise((resolve) => {
		// API30
		const currentTime = new Date();
		currentTime.setMinutes(currentTime.getMinutes() + 30);
		resolve({
			code: 200,
			msg: "success",
			data: currentTime.toISOString()
		});
	});
}
